@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap');

.item {
    height: 48px;

    font-style: normal;
    font-weight: 300;
    font-size: 14px;
    line-height: 16px;

    border-bottom: 1px solid #E5E5E5;


}

.item:last-of-type {
    border: none;
}

.users {
    width: 100%;
}

.buttonsContainer {
    margin-bottom: 18px;
    text-align: right;
}

.container {
    width: 600px;
    height: 400px;
}

.thead {
    height: 36px;
    background: #E5E5E5;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;

    color: #000000;
}

.nameCol {
    vertical-align: middle;
    width: 300px;
    padding-left: 17px;
}

.ageCol {
    vertical-align: middle;
    font-weight: 700;
}